// Lazysizes
import 'lazysizes';
import 'lazysizes/plugins/bgset/ls.bgset.js';
import 'lazysizes/plugins/respimg/ls.respimg.js';
import 'lazysizes/plugins/parent-fit/ls.parent-fit.js';

// JQ
import jquery from 'jquery';
window.jQuery = window.$ = jquery;

import Swiper, { Navigation, Pagination,  Scrollbar, EffectFade, Thumbs } from "swiper";
Swiper.use([Navigation, Pagination,  Scrollbar, EffectFade, Thumbs]);

// LIBS
// require('./_bower/wow/dist/wow');
// ??
// import './_bower/wow/dist/wow.js'; ???


document.addEventListener('DOMContentLoaded', () => {
    /* main page news slider */
    if($(window).width() <= 1024){
        const stocksSlider = new Swiper('#stocks-slider', {
        speed: 800,
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        breakpoints: {
            570: {
                slidesPerView: 2,
                spaceBetween: 10
            },
            768: {
                slidesPerView: 2,
                spaceBetween: 20
            },
            1024: {
                slidesPerView: 2,
                spaceBetween: 40
            }
        },
        navigation: {
            nextEl: '.stocks__content .nav-btn-row .btn-next',
            prevEl: '.stocks__content .nav-btn-row .btn-prev',
        },
    });
    }
    /* catalog single page similar slider */
    const similarSlider = new Swiper('#similar-slider', {
        speed: 800,
        slidesPerView: "auto",
        spaceBetween: 30,
        loop: false,
        scrollbar: {
            el: ".similar-slider .swiper-scrollbar",
            clickable: true
        }
    });
});